package components

import emotion.react.css
import js.objects.jso
import react.FC
import react.Props
import react.dom.html.ReactHTML
import react.dom.html.ReactHTML.div
import web.cssom.*

external interface InfoScreenProps: Props {
    var closeScreen: () -> Unit
}

val InfoScreen = FC<InfoScreenProps> { props ->
    div {
        css {
            position = Position.absolute
            background = rgb(255, 255, 255, 0.95)
            display = Display.flex
            alignItems = AlignItems.center
            justifyContent = JustifyContent.center
            width = 100.pct
            height = 100.pct
            top = 0.px
            left = 0.px
            zIndex = integer(100)
        }

        onClick = { e ->
            if (e.target == e.currentTarget)
                props.closeScreen()
        }

        ReactHTML.div {
            css {
                background = NamedColor.white
                maxWidth = min(700.px, 100.pct - 20.px)
                maxHeight = 100.pct - 20.px
                padding = 30.px
                borderRadius = 10.px
                boxSizing = BoxSizing.borderBox
                overflowY = Overflow.scroll
                position = Position.relative
                filter = dropShadow(2.px, 2.px, 3.px, NamedColor.gray)
                textAlign = TextAlign.justify
            }

            ReactHTML.h1 {
                css {
                    textAlign = TextAlign.center
                    marginTop = 0.px
                }
                +"Information"
            }
            +"This page provides a web-based user interface to an implementation of SimpleSets, a visualization method that captures patterns in categorical point data. SimpleSets works in two main phases: first the points are partitioned into patterns, then a drawing is created of such a partition. SimpleSets uses two types of patterns: islands and banks. Intuitively, islands are convex hulls of input points and banks are sequences of input points. Dilated versions of the patterns form the basis of the drawing. The drawing makes use of an implicit third dimension in which the dilated patterns are stacked locally on top of each other. These dilated patterns are modified locally such that each point clearly lies only within the enclosing shape of its pattern. In the figure below an overview is given of the pipeline."
            ReactHTML.div {
                css {
                    width = 100.pct
                    margin = Margin(20.px, 0.px)
                }

